import { cancelAttention, deleteMyArticle, followUser, } from '@/common/requestFunctions';
import { getMyUserInfo } from '@/common/storageFunctions';
import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE, reportObjectType } from "@/common/constants";
/**
 * labelActivity 指定标签下的动态
 * @description 指定标签下的动态
 * @Author: 穆兰
 * @Date: 2022/1/7
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/17
 */
export default {
    data() {
        return {
            isRefresh: false,
            haveMoreData: false,
            labelId: '',
            labelContent: '',
            articles: [],
            myId: 0,
            editArticleItem: {},
            page: 1 //页号
        };
    },
    async onLoad(params) {
        this.labelId = params.labelId;
        this.labelContent = params.labelContent;
        let userInfo = await getMyUserInfo();
        if (userInfo) {
            this.myId = userInfo.id;
        }
        this.getLabelArticles(this.labelId, this.page);
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 刷新时
         */
        onIsRefresh() {
            this.isRefresh = true;
            this.page = 1;
            this.getLabelArticles(this.labelId, this.page);
        },
        /**
         * @description 滑到最底部时
         */
        onScrolltolower() {
            if (!this.haveMoreData) {
                return;
            }
            this.page = this.page + 1;
            this.getLabelArticles(this.labelId, this.page);
        },
        /**
         * @description 点击动态上的更多按钮
         */
        onMoreClick(item) {
            this.$refs.articleActionPopup.open();
            this.editArticleItem = item;
        },
        /**
         * @description 点击popup的举报按钮
         * @param {ArticleItem} item
         */
        onPopupReportClick(item) {
            uni.navigateTo({
                url: `/pages/setting/report-user/report-user?objectId=${item.id}&objectType=${reportObjectType.activity}`
            });
            this.$refs.articleActionPopup.close();
        },
        /**
         * @description 点击popup的删除按钮
         * @param item
         */
        onPopupDeleteClick(item) {
            let _this = this;
            uni.showModal({
                title: '删除动态',
                content: '你确定要删除这个动态吗',
                success: async function (res) {
                    if (res.confirm) {
                        if (await deleteMyArticle(item.id)) {
                            let articles = _this.articles;
                            for (let i = 0; i < articles.length; i++) {
                                if (articles[i].id == item.id) {
                                    articles.splice(i, 1);
                                    break;
                                }
                            }
                            _this.articles = articles;
                            _this.$refs.articleActionPopup.close();
                            uni.showToast({
                                title: '删除成功',
                                icon: 'success'
                            });
                        }
                    }
                    else {
                        console.log("用户点击取消");
                    }
                }
            });
        },
        /**
         * @description 点击popup/activity-item的取消关注按钮
         * @param item
         */
        async onCancelFollow(item) {
            if (!item.publisher) {
                return;
            }
            let _this = this;
            uni.showModal({
                title: '取消关注',
                content: `你确定要取消关注${item.publisher.username}`,
                success: async function (res) {
                    if (res.confirm) {
                        if (item.publisher)
                            if (await cancelAttention(item.publisher.id)) {
                                let articles = _this.articles;
                                for (let i = 0; i < articles.length; i++) {
                                    if (articles[i].publisher && articles[i].publisher
                                        .id == item.publisher.id) {
                                        articles[i].publisher.isAttention = 0;
                                    }
                                }
                                _this.articles = articles;
                                _this.$refs.articleActionPopup.close();
                                uni.showToast({
                                    title: '取消关注',
                                    icon: 'success'
                                });
                            }
                    }
                    else if (res.cancel) {
                        console.log('用户点击取消');
                    }
                }
            });
        },
        /**
         * @description 点击popup/activity-item的关注按钮
         *  @param item
         */
        async onFollow(item) {
            if (!item.publisher) {
                return;
            }
            let _this = this;
            if (await followUser(item.publisher.id)) {
                let articles = _this.articles;
                for (let i = 0; i < articles.length; i++) {
                    if (articles[i].publisher && articles[i].publisher.id == item.publisher.id) {
                        articles[i].publisher.isAttention = 1;
                    }
                }
                _this.articles = articles;
                _this.$refs.articleActionPopup.close();
                uni.showToast({
                    title: '关注成功',
                    icon: 'success'
                });
            }
        },
        /**
          * @description 获取指定标签下的动态
          * @param page 页号
          * @param labelId 标签的Id
          */
        async getLabelArticles(labelId, page) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'activity',
                    action: 'groupByLabel',
                    data: {
                        page: page,
                        labelId: labelId
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            setTimeout(() => {
                this.isRefresh = false;
            }, 700);
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                let pageSum = res.data.data.pageSum;
                let articles = res.data.data.list;
                console.log(articles);
                if (page < pageSum) { //判断是不是最后一页
                    this.haveMoreData = true;
                }
                else {
                    this.haveMoreData = false;
                }
                if (page === 1) {
                    uni.showToast({
                        title: '刷新成功',
                        icon: "none"
                    });
                    this.articles = articles;
                    this.scrollInto = '';
                }
                else {
                    let allArtilces = this.articles.concat(articles);
                    this.articles = allArtilces;
                }
                if (articles.length < 1) { //如果这一页没有动态则切换到下一页
                    if (page < pageSum) {
                        this.page = page + 1;
                        this.getLabelArticles(labelId, this.page);
                    }
                }
            }
        }
    }
};
